.hero {
    position: relative;
    width: 100%;
    height: 1400px; /* Adjust height as needed */
    background: url('/Users/hannahvile/Desktop/mikanft/src/art/mikaart0.gif') center center/cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .hero-content {
    text-align: center;
    color: #fff;
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .hero-content p {
    font-size: 1.2rem;
    margin-top: 10px;
  }
  
  .hero-content button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    font-size: 1rem;
    margin-top: 20px;
    cursor: pointer;
  }
  
  /* Media query for mobile responsiveness */
  @media (max-width: 768px) {
    .hero {
      height: 1000px; /* Adjust height for smaller screens */
    }
  
    .hero-content {
      padding: 0 20px;
    }
  
    .hero-content h1 {
      font-size: 2rem;
    }
  
    .hero-content p {
      font-size: 1rem;
    }
  }
  