.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #333;
  color: white;
}

.logo img {
  width: 100px; /* Adjust width as needed */
  height: auto;
}

.nav {
  display: flex;
}

.nav ul {
  list-style: none;
  display: flex;
}

.nav ul li {
  margin: 0 10px;
}

.nav ul li a {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav ul li a:hover {
  color: #ddd;
}

.hamburger-menu {
  display: none; /* Hide hamburger menu by default */
  cursor: pointer;
}

.hamburger-menu .bar {
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 5px 0;
  transition: 0.4s;
}

/* Media query for mobile responsiveness */
@media (max-width: 768px) {
  .logo img {
    width: 80px; /* Adjust width for smaller screens */
  }

  .nav {
    display: none; /* Hide navigation menu by default for smaller screens */
    flex-direction: column;
    background-color: #333;
    position: absolute;
    top: 60px; /* Adjust top position as needed */
    left: 0;
    width: 100%;
    padding: 10px;
    text-align: center;
  }

  .nav.open {
    display: flex;
  }

  .hamburger-menu {
    display: block; /* Show hamburger menu for smaller screens */
  }
}
